        <span>JSONå¤„ç†</span>
      </div>
      
      <el-form :model="form" label-width="120px">
        <el-form-item label="æ“ä½œç±»å‹">
          <el-radio-group v-model="form.operation" @change="handleProcess">
            <el-radio label="format">æ ¼å¼åŒ–</el-radio>
            <el-radio label="compress">å‹ç¼©</el-radio>
            <el-radio label="validate">éªŒè¯</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="ç¼©è¿›è®¾ç½®" v-if="form.operation === 'format'">
          <el-select v-model="form.indentSize" @change="handleProcess">
            <el-option label="2ä¸ªç©ºæ ¼" :value="2"></el-option>
            <el-option label="4ä¸ªç©ºæ ¼" :value="4"></el-option>
            <el-option label="Tabç¼©è¿›" value="tab"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="è¾“å…¥JSON">
          <el-input
            type="textarea"
            v-model="form.inputJson"
            placeholder="è¯·è¾“å…¥JSONæ•°æ®"
            :rows="12"
            @input="handleProcess"
          ></el-input>
          <div class="button-group">
            <el-button @click="clearInput">
              <i class="el-icon-delete"></i> æ¸…ç©º
            </el-button>
            <el-button @click="loadSample">
              <i class="el-icon-document"></i> åŠ è½½ç¤ºä¾‹
            </el-button>
          </div>
        </el-form-item>

        <el-form-item label="å¤„ç†ç»“æœ" v-if="form.operation !== 'validate'">
          <el-input
            type="textarea"
            v-model="form.outputJson"
            placeholder="å¤„ç†ç»“æœå°†æ˜¾ç¤ºåœ¨è¿™é‡Œ"
            :rows="12"
            readonly
          ></el-input>
          <div class="button-group">
            <el-button type="primary" @click="copyToClipboard(form.outputJson)">
              <i class="el-icon-copy-document"></i> å¤åˆ¶ç»“æœ
            </el-button>
            <el-button @click="downloadJson">
              <i class="el-icon-download"></i> ä¸‹è½½JSON
            </el-button>
          </div>
        </el-form-item>

        <el-form-item label="éªŒè¯ç»“æœ" v-if="form.operation === 'validate'">
          <el-alert
            v-if="validationResult.isValid"
            title="JSONæ ¼å¼æ­£ç¡®"
            type="success"
            :description="validationResult.message"
            show-icon
          ></el-alert>
          <el-alert
            v-else
            title="JSONæ ¼å¼é”™è¯¯"
            type="error"
            :description="validationResult.message"
            show-icon
          ></el-alert>
        </el-form-item>

        <el-form-item label="JSONä¿¡æ¯" v-if="jsonInfo.size > 0">
          <el-descriptions :column="2" border>
            <el-descriptions-item label="å­—ç¬¦æ•°">{{ jsonInfo.size }}</el-descriptions-item>
            <el-descriptions-item label="å¯¹è±¡æ•°">{{ jsonInfo.objects }}</el-descriptions-item>
            <el-descriptions-item label="æ•°ç»„æ•°">{{ jsonInfo.arrays }}</el-descriptions-item>
            <el-descriptions-item label="å±æ€§æ•°">{{ jsonInfo.properties }}</el-descriptions-item>
          </el-descriptions>
