<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'JsonFormatterView',
  metaInfo: {
    title: 'JSONæ ¼å¼åŒ–å·¥å…· - åœ¨çº¿JSONç¾åŒ–å‹ç¼©éªŒè¯',
    meta: [
      { name: 'description', content: 'åœ¨çº¿JSONæ ¼å¼åŒ–å·¥å…·ï¼Œæ”¯æŒJSONç¾åŒ–ã€å‹ç¼©ã€éªŒè¯ï¼Œæä¾›è¯­æ³•æ£€æŸ¥å’Œé”™è¯¯å®šä½åŠŸèƒ½ã€‚' },
      { name: 'keywords', content: 'JSONæ ¼å¼åŒ–,JSONç¾åŒ–,JSONå‹ç¼©,JSONéªŒè¯,åœ¨çº¿å·¥å…·' }
    ]
  },
  data() {
    return {
      form: {
        operation: 'format',
        indentSize: 2,
        inputJson: '',
        outputJson: ''
      },
      validationResult: {
        isValid: true,
        message: ''
      },
      jsonInfo: {
        size: 0,
        objects: 0,
        arrays: 0,
        properties: 0
      }
    }
  },
  methods: {
    handleProcess() {
      if (!this.form.inputJson.trim()) {
        this.form.outputJson = ''
        this.resetValidation()
        this.resetJsonInfo()
        return
      }

      try {
        const parsed = JSON.parse(this.form.inputJson)
        this.updateJsonInfo(this.form.inputJson, parsed)

        switch (this.form.operation) {
          case 'format':
            this.formatJson(parsed)
            break
          case 'compress':
            this.compressJson(parsed)
            break
          case 'validate':
            this.validateJson()
            break
        }
      } catch (error) {
        this.handleJsonError(error)
      }
    },

    formatJson(parsed) {
      const indent = this.form.indentSize === 'tab' ? '\t' : ' '.repeat(this.form.indentSize)
      this.form.outputJson = JSON.stringify(parsed, null, indent)
      this.validationResult = { isValid: true, message: 'JSONæ ¼å¼æ­£ç¡®' }
    },

    compressJson(parsed) {
      this.form.outputJson = JSON.stringify(parsed)
      this.validationResult = { isValid: true, message: 'JSONæ ¼å¼æ­£ç¡®' }
    },

    validateJson() {
      this.validationResult = { 
        isValid: true, 
        message: 'JSONæ ¼å¼æ­£ç¡®ï¼Œè¯­æ³•æ— è¯¯' 
      }
    },

    handleJsonError(error) {
      this.form.outputJson = ''
      this.validationResult = {
        isValid: false,
        message: `è¯­æ³•é”™è¯¯ï¼š${error.message}`
      }
      this.resetJsonInfo()
    },

    updateJsonInfo(jsonString, parsed) {
      this.jsonInfo = {
        size: jsonString.length,
        objects: this.countObjects(parsed),
        arrays: this.countArrays(parsed),
        properties: this.countProperties(parsed)
      }
    },

    countObjects(obj) {
      let count = 0
      if (typeof obj === 'object' && obj !== null && !Array.isArray(obj)) {
        count = 1
        for (const key in obj) {
          count += this.countObjects(obj[key])
        }
      } else if (Array.isArray(obj)) {
        for (const item of obj) {
          count += this.countObjects(item)
        }
      }
      return count
    },

    countArrays(obj) {
      let count = 0
      if (Array.isArray(obj)) {
        count = 1
        for (const item of obj) {
          count += this.countArrays(item)
        }
      } else if (typeof obj === 'object' && obj !== null) {
        for (const key in obj) {
          count += this.countArrays(obj[key])
        }
      }
      return count
    },

    countProperties(obj) {
      let count = 0
      if (typeof obj === 'object' && obj !== null && !Array.isArray(obj)) {
        count = Object.keys(obj).length
        for (const key in obj) {
          count += this.countProperties(obj[key])
        }
      } else if (Array.isArray(obj)) {
        for (const item of obj) {
          count += this.countProperties(item)
        }
      }
      return count
    },

    resetValidation() {
      this.validationResult = { isValid: true, message: '' }
    },

    resetJsonInfo() {
      this.jsonInfo = { size: 0, objects: 0, arrays: 0, properties: 0 }
    },

    clearInput() {
      this.form.inputJson = ''
      this.form.outputJson = ''
      this.resetValidation()
      this.resetJsonInfo()
    },

    loadSample() {
      this.form.inputJson = `{
  "name": "å¼ ä¸‰",
  "age": 30,
  "city": "åŒ—äº¬",
  "hobbies": ["è¯»ä¹¦", "æ¸¸æ³³", "ç¼–ç¨‹"],
  "address": {
    "street": "ä¸­å…³æ‘å¤§è¡—",
    "number": 123,
    "zipcode": "100080"
  },
  "married": true,
  "children": null
}`
      this.handleProcess()
    },

    async copyToClipboard(text) {
      if (!text) {
        this.$message.warning('æ²¡æœ‰å¯å¤åˆ¶çš„å†…å®¹')
        return
      }

      try {
        const success = await ClipboardUtil.copyText(text)
        if (success) {
          this.$message.success('å·²å¤åˆ¶åˆ°å‰ªè´´æ¿')
        } else {
          this.$message.error('å¤åˆ¶å¤±è´¥')
        }
      } catch (error) {
        this.$message.error('å¤åˆ¶å¤±è´¥: ' + error.message)
      }
    },

    downloadJson() {
      if (!this.form.outputJson) {
        this.$message.warning('æ²¡æœ‰å¯ä¸‹è½½çš„å†…å®¹')
        return
      }

      DownloadUtil.downloadJSON(JSON.parse(this.form.outputJson), 'formatted.json')
      this.$message.success('æ–‡ä»¶ä¸‹è½½æˆåŠŸ')
    }
  }
}
</script>

